export function formatReadingTime(minutes) {
  let cups = Math.round(minutes / 5)
  if (cups > 5) {
    if (cups < 20) {
      return `${new Array(Math.round(cups / Math.E)).fill("🍱").join("")} ${minutes} min read`
    }
    return `${new Array(Math.round(cups / 5)).fill("🥘").join("")} ${minutes} min read`
  } else {
    return `${new Array(cups || 1).fill("☕️").join("")} ${minutes} min read`
  }
}

export function transformPostUrl(postUrl) {
  // https://api.github.com/repos/shanejix/shanejix.github.io/issues/25
  // https://github.com/shanejix/shanejix.github.io/issues/25
  return "https://" + postUrl.slice(12).replace(/\/repos/g, "")
}
